import React from 'react'
import {connect} from 'react-redux';


const Todo = (props) => {
    console.log(props);
    return (
        <div>
            {
                props.todos.todo.map((item,idx)=>(
                <li key={idx}>
                    {item}
                    <button onClick={()=>{
                        props.dispatch({
                            type:'DEL_TODO',
                            index:idx
                        })
                    }}>删除</button>
                </li>))
            }
        </div>
    )
}

const mapStateToProps = (state)=>{//state 为store中的状态
    return {
        todos:state
    }
}

export default connect(mapStateToProps) (Todo)
