let redu = {
    datalist:[],
    shoucang:[],
    userinfo:''
}

const home = (state=redu,action)=>{
    switch(action.type){
        case 'DATA_IN':
            redu = Object.assign({},state,{
                datalist:[
                    ...state.datalist,
                    {
                        ...action.datalist
                    }
                ]
            });
            return redu;
        case 'CHANGE':
            let num = action.index;
            redu = Object.assign({},state,{
                shoucang:[
                    ...state.shoucang,
                    {
                        ...state.datalist[num]
                    }
                ]
            })
            return redu;
        case 'DEL_TODO':
            let arr = state.shoucang;
            arr.splice(action.index,1);
            redu = Object.assign({},state,{
                shoucang:[
                    ...arr
                ]
            })
            return redu;
        case 'USER_NAME':
            redu = Object.assign({},state,{userinfo:action.name})
            return redu;
        default:
            return state;
    }
}

export default home;