import React, { Component } from 'react'
import List from '../components/List'
import Input from '../components/Input'

export default class ToDoList extends Component {
    constructor(props){
        super();
        this.state = {
            inpValue : '',
            todos : [
                {value:'1',done:false},
                {value:'2',done:true},
                {value:'3',done:false},
                {value:'4',done:true}
            ]
        }
    }
    addToDo = (inpValue)=>{
        if(inpValue!==""){
            this.setState({
                todos:[
                    {
                        value:this.state.inpValue,
                        done:false
                    },
                    ...this.state.todos
                ],
                inpValue:''
            })
        }else{
            alert("添加不能为空！");
        }   
    }
    componentDidMount(){
        // this.input.focus();
        let todo = localStorage.getItem('todos');
        if(todo){
            this.setState({
                todos:JSON.parse(todo)
            })
        }
    }
    componentDidUpdate(preProps,preState){
        if(preState.todos!==this.state.todos){
            localStorage.setItem(
                'todos',
                JSON.stringify(this.state.todos)
            )
        }
    }
    delToDo = (idx)=>{
        this.setState(
            {todos:this.state.todos.filter((item,index)=>index!=idx)},
            ()=>{
                localStorage.setItem(
                    'todos',
                    JSON.stringify(this.state.todos)
                )
            }
        )
    } 
    transfrom = (idx)=>{
        let newToDo = JSON.parse(JSON.stringify(this.state.todos));
        newToDo[idx].done = !newToDo[idx].done;
        this.setState({
            todos:newToDo
        })
    }

    render() {
        return (
            <div className='ToDoList'>
                <Input 
                addToDo={this.addToDo}
                />
                {/* 可将input变成双标签，标签内嵌套HTML结构，在子组件通过this.props.children渲染HTML结构 */}
                <List 
                todos={this.state.todos}
                transfrom={this.transfrom}
                delToDo={this.delToDo}
                />
                
            </div>
        )
    }
}
