import { connect } from 'react-redux';
import React from 'react';
import { Link } from 'react-router-dom';
import './List.css';

const List = (props) => {
    return (
        <div>
            {
                props.datalist.home.datalist.map((item, idx) => (
                    <Link to={{ pathname: '/home/list1' ,index:idx}}>
                        <div style={{ width: '45%' }} className='list'>
                            <img className='pic' src={item.img} />
                            <span className='span1'>{item.title}</span>
                            <span className='span2'><span className='span3'>￥ {item.price} </span>  675人已购买</span>
                        </div>
                    </Link>
                ))
            }
        </div>
    )
}

const mapStateToProps = (state) => {//state 为store中的状态
    return {
        datalist: state
    }
}


export default connect(mapStateToProps)(List);



