import React from 'react';
import { connect } from 'react-redux';
import {Redirect} from 'react-router-dom';

const Cart = (props) => {
    let shoucangArr = props.datalist.home.shoucang;
    let username = props.datalist.home.userinfo;
    let location = props.location.pathname;
    return (
        <div>
            {
                username == '' ? <Redirect
                to={{
                  pathname: "/login",
                  state: location 
                }}/> :
                    <ul>
                        {
                            shoucangArr.map((item, index) => (
                                <li>
                                    {item.title}
                                    <button onClick={() => {
                                        props.dispatch({
                                            type: 'DEL_TODO',
                                            index: index
                                        })
                                    }}>删除</button>
                                </li>

                            ))
                        }
                    </ul>
            }
        </div>
    )
}

const mapStateToProps = (state) => {//state 为store中的状态
    return {
        datalist: state
    }
}
export default connect(mapStateToProps)(Cart);
