import React, { Component } from 'react'

export default class Input extends Component {
    constructor(props){
        super();
        this.state = {
            inpValue:''
        }
    }
    handleChange = (e)=>{
        this.setState({
            inpValue:e.target.value//e.target 返回触发事件的节点，即input ,将input的value属性赋给inpValue
        })
    }
    render() {
        return (
            <div>
                <label htmlFor='inp'>todolist</label>
                <input
                  id='inp'
                  type='text' 
                  onChange={(e)=>this.handleChange(e)} 
                  value={this.props.inpValue}
                />
                
                <button onClick={()=>this.props.addToDo(this.state.inpValue)}>添加</button>
            </div>
        )
    }
}
