import React from 'react';
import Rotation from './Rotation'
import { connect } from 'react-redux'
import { login } from './actionCreator';
import './Home.css';
import List from './List';
import { NavLink } from 'react-router-dom';
import { Provider } from 'react-redux';
import store from './store';

const Home = (props) => {
    window.onload = function () {
        props.dispatch(login())
    }
    return (
        <Provider store={store}>
            <div className='head'>
                <i className='iconfont icon-taobao3' />
                <NavLink to='/home/searchbar' className='nav1'>
                    <i className='iconfont icon-chazhao' />
                    <span className='sp'>寻找宝贝店铺</span>
                </NavLink>
            </div>
            <Rotation />
            <div className='line'>
                <img src='https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png' />
            </div>
            <List />
        </Provider>
    )
}

const mapStateToProps = (state) => ({
    datalist: state
})

export default connect(mapStateToProps)(Home);

