import React from 'react'
import { Carousel, WingBlank } from 'antd-mobile';
import './Rotation.css'

class Rotation extends React.Component {
    state = {
      data: [1,2,3,4],
      imgHeight: 131,
    }
    componentDidMount() {
      setTimeout(() => {
        this.setState({
          data: ['https://gw.alicdn.com/imgextra/i1/165/O1CN01jwzATW1D5byruAUQW_!!165-0-lubanu.jpg', 'https://gw.alicdn.com/imgextra/i3/178/O1CN010ekMVv1DBZ8All9dx_!!178-0-ppp-picassogw.jpg', 'https://gw.alicdn.com/imgextra/i2/200/O1CN01Tdawkc1DLdqR2Urtm_!!200-0-lubanu.jpg', 'https://gw.alicdn.com/imgextra/i4/62/O1CN012Icbap1CKRBacZI2H_!!62-0-lubanu.jpg', 'https://aecpm.alicdn.com/simba/img/TB1tEYNUeH2gK0jSZJnSuuT1FXa.jpg_q50.jpg'],
        });
      }, 100);
    }
    
    render() {
      return (
        <WingBlank>
            <Carousel
              autoplay={true}//是否自动切换
              infinite//是否循环播放//切换版面后的回调函数
            >
                {this.state.data.map(val => (

                    <a
                      key={val}
                      href="http://www.alipay.com"
                      style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                    >
                        <img
                          src={val}
                          alt=""
                          style={{ width: '100%', verticalAlign: 'top' }}
                          onLoad={() => {
                              // fire window resize event to change height
                              window.dispatchEvent(new Event('resize'));
                              this.setState({ imgHeight: 'auto' });
                              }
                            }
                        />
                    </a>
                ))}
            </Carousel>
        </WingBlank>
      );
    }
}
  
export default Rotation;

