import React from 'react';
import './List1.css';
import { connect } from 'react-redux';


const List1 = (props) => {
    let url = props.datalist.home.datalist;
    let idx = props.location.index;
    let dataid = url[idx].id;
    let arr = props.datalist.home.shoucang;
    let name = 'iconfont icon-shoucang1';
    let username = props.datalist.home.userinfo;
    let location = props.location.pathname;
    for (let i = 0; i < arr.length; i++) {
        if (arr[i].id == dataid) {
            name = 'iconfont icon-shoucang';
            break;
        }
        name = 'iconfont icon-shoucang1';
    }

    const change = (e) => {
        if(username==''){
            props.history.push({pathname:'/login',state:location,index:idx});
        }
        let name = e.target.className
        if (name == 'iconfont icon-shoucang1') {
            props.dispatch({
                type: 'CHANGE',
                index: idx
            })
        }else{
            props.dispatch({
                type:'DEL_TODO',
                index:idx
            })
        }
    }

    return (
        <div>
            <img className='pic' src={url[idx].img} style={{ width: '187.416px', height: '187.416px' }} />
            <button onClick={() => { props.history.push('/') }}>返回</button>
            <div className='footer'>
                <div className='tubiao'>
                    <div className='f1'>
                        <i id='i' className='iconfont icon-dianpu'></i>
                        <p>店铺</p>
                    </div>
                    <div className='f1'>
                        <i id='i' className='iconfont icon-aliwangwang'></i>
                        <p>客服</p>
                    </div>
                    <div className='f1'>
                        <i id='i' className={name}
                            onClick={(e) => { change(e) }}>
                        </i>
                        <p>收藏</p>
                    </div>
                </div>

                <div className='f2'>
                    <div className='join'>
                        <span>加入购物车</span>
                    </div>
                    <div className='buy'>
                        <span>立刻购买</span>
                    </div>
                </div>
            </div>
        </div>
    )
}


const mapStateToProps = (state) => {//state 为store中的状态
    return {
        datalist: state
    }
}

export default connect(mapStateToProps)(List1);
