import React from 'react';
import { TabBar } from 'antd-mobile';
import {withRouter} from 'react-router-dom';
import './Mytab.css';

class Mytab extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // selectedTab:'blueTab',
    };
  }


  render() {
    const {pathname} = this.props.location;
    if(pathname.split('/').length>2){
      return null;
    }
    console.log(pathname);
    // if(pathname){
    //   return null;
    // }
    return (
      <div style={{ position: 'fixed',  width: '100%', bottom: 0 }} className='box'>   
      {/* //fixed!important  !important可将属性设置为最高优先级*/}
        <TabBar
          unselectedTintColor="#949494"
          tintColor="orange"
          barTintColor="white"
        >
          <TabBar.Item
            title="首页"
            key="home"
            icon={
              <i style={{fontSize:21}} className='iconfont icon-shouye'></i>
            }
            selectedIcon={
              <i style={{fontSize:21}} className='iconfont icon-taobao'></i>
            }
            selected={pathname === '/'}
            onPress={() => {
              this.props.history.push('/');
            }}
            data-seed="logId"
          >
            
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-gouwuche1'></i>
            }
            selectedIcon={
              <i className='iconfont icon-gouwuche'></i>
            }
            title="收藏"
            key="shopcar"
            selected={pathname==='/cart'}
            onPress={() => {
              this.props.history.push('/cart');
            }}
            data-seed="logId1" 
          >
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-dingdanliebiao'></i>
            }
            selectedIcon={
              <i className='iconfont icon-dingdanliebiao-tianchong'></i>
            }
            title="订单列表"
            key="shoplist"
            selected={pathname === '/list'}
            onPress={() => {
              this.props.history.push('/list');
            }}
          >
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-wode'></i>
              }
            selectedIcon={
              <i className='iconfont icon-wodedangxuan'></i>
            }
            title="我的淘宝"
            key="myTaobao"
            selected={pathname === '/mytb'}
            onPress={() => {
              this.props.history.push('/mytb');
            }}
          >
          </TabBar.Item>
          <TabBar.Item
            icon={
              <i className='iconfont icon-gengduo'></i>
            }
            selectedIcon={
              <i className='iconfont icon-19'></i>
            }
            title="更多"
            key="more"
            selected={pathname === '/more'}
            onPress={() => {
              this.props.history.push('/more');
            }}
          >
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}

export default withRouter(Mytab)
