import React, { Component } from 'react'

class List extends Component {
    constructor(props){
        super();
    }
    toDoList = (done)=>{
        return this.props.todos.map((items,indx)=>{
            if(items.done==done){
                return  <li>
                <input type='checkbox' onClick={()=>{this.props.transfrom(indx)}} checked={items.done}/>
                <span>{items.value} </span>
                <button onClick={()=>{this.props.delToDo(indx)}}>删除</button>
            </li>
                
            }
        })
    }
    render() {
        let arr1 = this.props.todos.filter(item=>!item.done);
        let arr2 = this.props.todos.filter(item=>item.done);
        return (
            <div>
                <h2>正在进行{arr1.length}</h2>
                <ul>
                    {this.toDoList(false)}
                </ul>
            <h2>已经完成{arr2.length}</h2>
                <ul>
                    {this.toDoList(true)}
                </ul>
            </div>
        )
    }
}


export default List
