import React from 'react'
import { connect } from 'react-redux';

const Login = (props) => {
    const login = ()=>{
        props.dispatch({
            type:'USER_NAME',
            name:'xy'
        })
        props.history.push({pathname:props.location.state,index:props.location.index})
    }
    return (
        <div>
            <button onClick={login}>登录</button>
        </div>
    )
}

const mapStateToProps = (state) => {//state 为store中的状态
    return {
        datalist: state
    }
}

export default connect(mapStateToProps) (Login);