//reducer-是一个纯函数，没有副作用，有相同的输入，一定会有相同的输出

let todo = [1,2,3]

function todolist(state=todo,action){
    if(action.type=='ADD_TODO'){//type值一般为大写
        //不可变对象
        console.log("reducer",action)
        return [...state,action.value]
    }
    if(action.type=='DEL_TODO'){//type值一般为大写
        //不可变对象
        let arr = [...state];
        arr.splice(action.index,1)
        return arr;
    }
    return state;
}

export default todolist;